// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Row, Col, Card } from 'react-materialize';
// importando a foto do usuário (note que depois podemos usa-la facilmente chamando apenas avatar ao invés do caminho completo)
import avatar from '../../images/avatar.png';

import facebook from '../../images/facebook.png';

import github from '../../images/github.png';

const imgStyle={
  width: '24%', 
  marginLeft:'20%'
    
};
const imgStyle2={
  width: '24%',
  marginLeft:'10%'
    

    
};
const UserProfile = () => (
  
  <Card>
      <Row>
        <Col s={8} m={8} offset="s2 m2">
          <img src={avatar} className="circle responsive-img" />
        </Col>
      </Row>
      <Row className="center-align">
        <h5 >Lucas Peres</h5>
        <p className="grey darken-2 white-text">Desenvolvedor Web</p>
        
        
        <p className="blue darken-2 white-text">Mobile</p>


        
        <p className="red darken-2 white-text">Entusiasta</p>
        
      </Row>
      <Row>
      
            <a href='https://www.facebook.com/The.only.broken.hearted.loser.you.will.ever.need'><img src={facebook} style={imgStyle} href='https://www.facebook.com/The.only.broken.hearted.loser.you.will.ever.need'  /></a>
      
       
             <a href='https://github.com/Nevermre'><img src={github} style={imgStyle2} href='https://www.facebook.com/The.only.broken.hearted.loser.you.will.ever.need' className=" responsive-img" /></a>
         
      </Row>

      
  </Card>
);

export default UserProfile;