
// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Navbar, NavItem, Row} from 'react-materialize';



const Header = () => (
  <Row>  
    <Navbar brand={<a href='/' >Nvrmr</a>}  className="grey darken-2 ">
    
      <NavItem className=" right hide-on-med-and-down" href='/'>Inicio</NavItem>
      
      <NavItem className=" right hide-on-med-and-down" href='/tcc'>TCC</NavItem>
      
      <NavItem className=" sidenav hide-on-large-only "  href='/'>Inicio</NavItem>
      
      <NavItem className="  sidenav hide-on-large-only" href='/tcc'>TCC</NavItem>
   
    </Navbar>
     
  </Row>
);

export default Header;